import React from 'react'
import ListCard from '../Accessories/ListCard'
import ballOne from "../../../../static/ball_valves_four.jpg"
import ballTwo from "../../../../static/ball_valves_five.jpg"
import ballThree from "../../../../static/ball_valves_six.png"
import ballFour from "../../../../static/ball_valves_one.png"
import ballFive from "../../../../static/ball_valves_two.jpg"
import ballSix from "../../../../static/ball_valves_three.jpg"



function Content() {
  return (
    <div className='m-14'>
      <div class="grid grid-cols-3 gap-4">
        <div class="col-span-2 p-4">
          <div className='global-color text-2xl'><b>Ball Valves</b></div>
          <div className='my-2'>
            <b className='text-lg'>HIGH PRESSURE BALL VALVES</b> <br />
            <b>Hydraulic Sealing Solutions</b> offers High Pressure Ball Valves to suit a wide range of applications including mobile plant and industrial equipment.
          </div>
          <div className='my-2'>
            These valves should not be used as flow control valves and should therefore always be fully open or fully closed.
          </div>
          <div className='my-2'>
            <b className='text-lg'>FEATURES</b> <br />
            Full bore valves provide an unrestricted flow path for negligible pressure drop.
          </div>
          <div className='my-2'>
            Floating ball on pre-compressed seals minimizes wear and provides an excellent seal at positive and negative pressures.
          </div>
          <div className='my-2'>
            Operating temperatures from -20&deg;C to +100&deg;C.
          </div>
          <div className='my-2'>
            Open to close in 1/4 turn (90&deg;).
          </div>
          <div className='my-2'>
            Cranked handles are incorporated to provide good clearance for the operator’s hands.
          </div>
          <div className='my-2'>
            <b className='text-lg'>MATERIALS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>Body and port adaptors are Trivalent plated steel</li>
              <li>Handle – steel forging, Trivalent plated.</li>
              <li>Ball – carbon steel.</li>
              <li>Ball seals – DELRIN (polyacetalic resin)</li>
              <li>Shaft seal – nitrile</li>
            </ul>
          </div>
          <div className='my-2'>
            <b className='text-lg'>APPLICATIONS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>Ideal for use in the chemical, pharmaceutical, off-shore, food, petrochemical , rock breaker pipe lines and process industries</li>
              <li>A variety of special seals can be provided to comply with the stringent specifications required by these applications</li>
              <li>Specially designed ball valve for Rock Breaker fluid lines</li>
            </ul>
          </div>
          <div className='my-2'>
            <b className='text-lg'>PRODUCT DETAILS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>THREAD SIZE – ¼” – 2”</li>
              <li>THREAD TYPE – BSP, NPT, DIN</li>
              <li>OPERATING PRESSURE- UPTO 500 BAR</li>
              <li>OPERATING TEMPERATURE – -20&deg;C TO 100&deg;C</li>
            </ul>
          </div>
          <div className='my-5'>
            <div className='flex justify-evenly'>
              <div className='mr-2'>
                <img
                  src={ballFour}
                  alt="box one"
                />
              </div>
              <div className='ml-2 mr-2'>
                <img
                  src={ballFive}
                  alt='box four'
                />
              </div>
              <div className='ml-2'>
                <img
                  src={ballSix}
                  alt='adaptors five'
                />
              </div>
            </div>
          </div>
        </div>
        <div class="p-4 bg-white shadow">
          <ListCard />
        </div>
      </div>
      <div class="grid grid-cols-3 gap-4">
        <div class="col-span-2 p-4">
          <div className='global-color text-2xl'><b>Quick Release Couplings</b></div>
          <div className='my-2'>
            <b>Hydraulic Sealing Solutions</b> offers a variety of Quick Release Couplings to suit a wide range of applications including mobile plant and industrial equipment.
          </div>
          <div className='my-2'>
            <b className='text-lg'>ISO A SERIES</b> <br />
            ISO.A couplings are the ideal choice where a tough, durable product of superior quality is required.
          </div>
          <div className='my-2'>
            They are perfectly suited to industrial and agricultural applications. A poppet valve is used to minimize the pressure drop characteristics and provide a positive sealing arrangement.
          </div>
          <div className='my-2'>
            <b className='text-lg'>ISO A SERIES</b> <br />
            Positive, quick connection ensured by the snap together locking ball system.
          </div>
          <div className='my-2'>
            When connected the male and female halves can rotate, even under pressure, thus avoiding any torsional stress in the flexible hoses.
          </div>
          <div className='my-2'>
            Designed to withstand high pressure peaks and pulses.
          </div>
          <div className='my-2'>
            Poppet valves have balanced springs and calibrated oil flow passages to minimize the pressure drop.
          </div>
          <div className='my-2'>Poppet valve seals have shockproof protective edges</div>
          <div className='my-2'>
            <b className='text-lg'>MATERIALS</b> <br />
            Trivalent plated, heat treated carbon steel. Nitrile ‘O’ ring seals are fitted as standard. VITON seals are available on request.
          </div>
          <div className='my-2'>
            <b className='text-lg'>APPLICATIONS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>Ideal for use in the chemical, pharmaceutical, off-shore, food, petrochemical , rock breaker pipe lines and process industries</li>
              <li>A variety of special seals can be provided to comply with the stringent specifications required by these applications</li>
            </ul>
          </div>
          <div className='my-2'>
            <b className='text-lg'>PRODUCT DETAILS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>THREAD SIZE – ¼” – 2”</li>
              <li>THREAD TYPE – BSP, NPT, DIN</li>
              <li>OPERATING PRESSURE- UPTO 350 BAR</li>
            </ul>
          </div>
          <div className='my-2'>
            <b className='text-lg'>FLAT FACE/NON SPILL SERIES</b> <br />
            Flat Face couplings are the ideal choice where a tough, durable product of superior quality is required. They are perfectly suited to industrial and agricultural applications.
          </div>
          <div className='my-2'>
            Suitable for leak free performance in high pressure pulse, hammer circuits and many other applications
          </div>
          <div className='my-2'>
            <b className='text-lg'>FEATURES</b> <br />
            Safety locking sleeve fitted as standard to prevent accidental disconnection
          </div>
          <div className='my-2'>
            Flat faces are easily wiped clean
          </div>
          <div className='my-2'>
            Allows for minimal inclusion of air and contaminants during connection
          </div>
          <div className='my-2'>
            Flat face design prevents fluid loss during disconnection
          </div>
          <div className='my-2'>
            Bidirectional flow
          </div>
          <div className='my-2'>
            <b className='text-lg'>MATERIALS</b> <br />
            Trivalent plated, heat treated carbon steel. Nitrile ‘O’ ring seals are fitted as standard. VITON seals are available on request.
          </div>
          <div className='my-2'>
            <b className='text-lg'>APPLICATIONS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>Ideal for use in the chemical, pharmaceutical, off-shore, food, petrochemical , rock breaker pipe lines and process industries</li>
              <li>A variety of special seals can be provided to comply with the stringent specifications required by these applications</li>
            </ul>
          </div>
          <div className='my-2'>
            <b className='text-lg'>PRODUCT DETAILS</b>
          </div>
          <div className='my-2'>
            <ul className='ml-4'>
              <li>THREAD SIZE – ¼” – 2”</li>
              <li>THREAD TYPE – BSP, NPT, DIN</li>
              <li>OPERATING PRESSURE- UPTO 400 BAR</li>
              <li>OPERATING TEMPERATURE – -40&deg;C TO 106&deg;C (NITRILE SEAL)</li>
              <li>-20&deg;C TO 200&deg;C (VITON SEAL)</li>
            </ul>
          </div>
          <div className='my-5'>
            <div className='flex justify-evenly'>
              <div className='mr-2'>
                <img
                  src={ballOne}
                  alt="box one"
                />
              </div>
              <div className='ml-2 mr-2'>
                <img
                  src={ballTwo}
                  alt='adaptors four'
                />
              </div>
              <div className='ml-2'>
                <img
                  src={ballThree}
                  alt='adaptors five'
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  )
}

export default Content

